.menu {
  background-color: $menuBackgroundColor;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;

    overflow-y: auto;
  }

  &__top {
    @include top-actions;
    margin-bottom: 44px;
  }

  &__list {
    margin: 0 0 30px;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;

    @include hover(border-color, $secondTextColor);
  }

  &__call {
    display: block;
    width: max-content;

    font-weight: 700;
    text-transform: uppercase;

    &--phone {
      margin-bottom: 16px;

      font-size: 16px;
      line-height: 27px;
    }

    &--action {
      padding-bottom: 8px;

      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;

      border-bottom: 1px solid $secondTextColor;
    }
  }
}
